.section{
	position: relative;
	padding: 20px 0 100px;
}
.head{
	position: relative;
	margin-bottom: 80px;
	&-slider{
		position: relative;
		&__img{
			position: relative;
			img{
				width: 100%;
				height: auto !important;
			}
		}
		&__video {
			position: relative;
			display: block;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			padding-top: 40.26%;
			overflow: hidden;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 1;
			}
			iframe {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border: 0;
				transform: scale(1.4);
			}
			& > a {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 1;
			}
		}
		.slick{
			&-dots{
				bottom: 30px;
			}
		}
	}
}
.hot{
	position: relative;
	&-box{
		position: relative;
		&__head{
			position: relative;
			&-img{
				position: relative;
				display: block;
				background-size: cover;
				background-position: center;
				background-repeat: no-repeat;
				padding-top: 130.55%;
				overflow: hidden;
				transition: all 0.3s ease;
				&:hover{
					opacity: 0.8;
				}
				img{
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
				}
			}
		}
		&__body{
			position: relative;
			text-align: center;
			padding: 0 70px;
		}
			&__list{
				position: relative;
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				margin-bottom: 50px;
				&-box{
					position: relative;
					width: calc(50% - 30px);
					&:not(:nth-last-child(-n+2)){
						margin-bottom: 60px;
					}
				}
					&-img{
						position: relative;
						display: block;
						background-size: cover;
						background-position: center;
						background-repeat: no-repeat;
						padding-top: 100%;
						overflow: hidden;
						transition: all 0.3s ease;
						&:hover{
							opacity: 0.8;
						}
						img{
							position: absolute;
							top: 0;
							left: 0;
							opacity: 0;
						}
					}
			}
			&__title{
				position: relative;
				max-width: 590px;
				margin: 0 auto;
				&:not(:last-child){
					margin-bottom: 25px;
				}
			}
			&__text{
				position: relative;
				max-width: 590px;
				color: $colorText;
				font-size: 18px;
				line-height: 1.5;
				margin: 0 auto;
				&:not(:last-child){
					margin-bottom: 40px;
				}
			}
	}
}
.main-catalog{
	position: relative;
	&-list{
		position: relative;
		padding: 0 60px;
		&__box{
			position: relative;
			text-align: center;
			overflow: hidden;
			&-img{
				position: relative;
				display: block;
				background-size: cover;
				background-position: center;
				background-repeat: no-repeat;
				padding-top: 130.5%;
				margin-bottom: 20px;
				overflow: hidden;
				transition: all 0.3s ease;
				&:hover{
					opacity: 0.8;
				}
				img{
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
				}
			}
			&-title{
				position: relative;
				color: $colorDarkGrey;
				font-size: 44px;
				font-family: 'Playfair Display', sans-serif;
				line-height: 1;
				letter-spacing: 0.025em;
				text-transform: uppercase;
				margin-bottom: 40px;
			}
			&-link{
				position: relative;
				.btn{
					width: 250px;
				}
			}
		}
	}
}
.main-about{
	position: relative;
	background: $colorLightBg;
	padding-top: 100px;
	&__list{
		position: relative;
		padding-top: 90px;
		[class^="col"]{
			&:first-child{
				.main-about__box{
					margin: 0 auto;
				}
			}
		}
	}
		&__box{
			position: relative;
			max-width: 450px;
			&-title{
				position: relative;
				color: $colorDarkGrey;
				font-size: 16px;
				font-weight: 700;
				letter-spacing: 0.0625em;
				text-transform: uppercase;
				margin-bottom: 20px;
			}
			&-text{
				position: relative;
				line-height: 1.9;
				&:not(:last-child){
					margin-bottom: 50px;
				}
				p{
					&:not(:last-child){
						margin-bottom: 30px;
					}
				}
			}
			&-link{
				position: relative;
				.btn{
					width: 250px;
				}
			}
		}
}
.benefits{
	position: relative;
	background: $colorLightBg;
	border-top: 1px solid $colorBg;
	&-list{
		position: relative;
		display: flex;
		&__box{
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			flex-grow: 1;
			flex-shrink: 0;
			flex-basis: 0;
			padding: 50px 30px;
			&:not(:last-child){
				border-right: 1px solid $colorBg;
			}
			&-icon{
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 45px;
				height: 40px;
				text-align: center;
				&:not(:last-child){
					margin-bottom: 20px;
				}
			}
			&-title{
				position: relative;
				max-width: 240px;
				color: $colorLightGrey;
				line-height: 1.75;
				letter-spacing: 0.075em;
				text-align: center;
				text-transform: uppercase;
			}
		}
	}
}
.content{
	position: relative;
	padding: 50px 0;
	h1, .h1{
		line-height: 1.25;
	}
	.title{
		max-width: 870px;
		margin-left: auto;
		margin-right: auto;
	}
}
.blog{
	position: relative;
	&-list{
		position: relative;
		padding-top: 50px;
		&__box{
			position: relative;
			text-align: center;
			margin-bottom: 50px;
			&-head{
				position: relative;
			}
				&-img{
					position: relative;
					display: block;
					background-size: cover;
					background-position: center;
					background-repeat: no-repeat;
					padding-top: 100%;
					overflow: hidden;
					transition: all 0.3s ease;
					&:hover{
						opacity: 0.8;
					}
					img{
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0;
					}
				}
			&-body{
				position: relative;
				padding-top: 20px;
			}
				&-date{
					position: relative;
					color: $colorText;
					margin-bottom: 15px;
				}
				&-title{
					position: relative;
					color: $colorDarkGrey;
					font-size: 22px;
					font-family: 'Playfair Display', sans-serif;
					line-height: 1.35;
					&:before{
						content: '';
						position: absolute;
						background: $colorBg;
						left: 50%;
						bottom: -15px;
						width: 60px;
						height: 1px;
						transform: translateX(-50%);
						opacity: 0;
					}
					&:not(:last-child){
						margin-bottom: 35px;
						&:before{
							opacity: 1;
						}
					}
					a{
						color: $colorDarkGrey;
						transition: color 0.3s ease;
						&:hover{
							color: $colorGrey;
						}
					}
				}
				&-category{
					position: relative;
					color: $colorText;
					font-size: 12px;
					letter-spacing: 0.0825em;
					a{
						color: $colorText;
						transition: color 0.3s ease;
						&:hover{
							color: $colorLigthText;
						}
					}
				}
		}
	}
	&-details{
		position: relative;
		max-width: 870px;
		margin: 0 auto 50px;
		&__date{
			position: relative;
			color: $colorText;
			text-align: center;
			margin-bottom: 20px;
		}
		.title{
			&:not(:last-child){
				margin-bottom: 40px;
				&:before{
					opacity: 1;
				}
			}
			&:before{
				content: '';
				position: absolute;
				background: $colorBg;
				left: 50%;
				bottom: -20px;
				width: 60px;
				height: 1px;
				transform: translateX(-50%);
				opacity: 0;
			}
			h1, .h1{
				line-height: 1.25;
			}
		}
		&__category{
			position: relative;
			color: $colorText;
			font-size: 12px;
			text-align: center;
			letter-spacing: 0.0825em;
			margin-bottom: 20px;
			a{
				color: $colorText;
				transition: color 0.3s ease;
				&:hover{
					color: $colorLigthText;
				}
			}
		}
		&__text{
			position: relative;
			color: $colorDarkGrey;
			line-height: 1.85;
			&:not(:last-child){
				margin-bottom: 50px;
			}
			img{
				display: block;
				height: auto !important;
				max-height: 470px;
				margin: 50px auto;
			}
			& > img{
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
			}
			h2, .h2{
				font-size: 22px;
				font-weight: 400;
				font-family: 'Playfair Display', sans-serif;
				text-transform: none;
				&:not(:last-child){
					margin-bottom: 20px;
				}
			}
			p{
				&:not(:last-child){
					margin-bottom: 20px;
				}
				&:first-child{
					img{
						&:first-child{
							margin-top: 0;
						}
					}
				}
				&:last-child{
					img{
						&:last-child{
							margin-bottom: 0;
						}
					}
				}
			}
			ul{
				margin: 20px 0;
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
				li{
					position: relative;
					padding-left: 15px;
					&:before{
						content: '';
						position: absolute;
						left: 0;
					}
				}
			}
			ol{
				margin: 20px 0;
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
				li{
					list-style-type: decimal;
					list-style-position: inside;
				}
			}
			a{
				color: $colorDarkGrey;
				text-decoration: underline;
				transition: color 0.3s ease;
				&:hover{
					color: $colorText;
					text-decoration: none;
				}
			}
			.table-responsive{
				margin: 50px 0;
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
			}
			table{
				width: 100%;
				border-collapse: collapse;
				margin: 50px 0;
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
				thead{
					tr{
						td,
						th{
							background: $colorBlack;
							color: $colorWhite;
							font-weight: 700;
							text-transform: uppercase;
						}
					}
				}
				tbody{
					tr{
						&:not(:first-child){
							td,
							th{
								border-top: 1px solid $colorBg;
							}
						}
						td,
						th{
							background: $colorLightBg;
						}
					}
				}
				tr{
					td,
					th{
						vertical-align: middle;
						height: 46px;
						color: $colorBlack;
						font-size: 12px;
						line-height: 1.35;
						text-align: center;
						letter-spacing: 0.0825em;
						padding: 15px 10px;
					}
				}
			}
			blockquote{
				position: relative;
				background: $colorLightBg;
				font-style: italic;
				text-align: center;
				padding: 50px;
				margin: 50px 0;
				&:first-child{
					margin-top: 0;
				}
				&:last-child{
					margin-bottom: 0;
				}
				p{
					&:not(:last-child){
						margin-bottom: 50px;
					}
				}
				footer{
					font-size: 16px;
					font-weight: 700;
					font-style: normal;
					letter-spacing: 0.0625em;
					text-transform: uppercase;
				}
			}
		}
		&__share{
			position: relative;
			display: flex;
			align-items: center;
			&-title{
				position: relative;
				color: $colorDarkGrey;
				letter-spacing: 0.075em;
				text-transform: uppercase;
				margin-right: 30px;
			}
		}
	}
}
.catalog{
	position: relative;
	padding: 0 15px;
	margin: 0 auto;
	&-category{
		position: relative;
		margin: 0 -15px 40px;
		ul{
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: center;
			li{
				margin: 0 15px 10px;
				a{
					position: relative;
					background: $colorLightBg;
					display: flex;
					align-items: center;
					justify-content: center;
					width: 270px;
					height: 80px;
					color: $colorDarkGrey;
					font-size: 16px;
					font-weight: 700;
					letter-spacing: 0.0625em;
					text-align: center;
					text-transform: uppercase;
					padding: 5px;
					overflow: hidden;
					transition: all 0.3s ease;
					&:hover,
					&.active{
						background: $colorDarkGrey;
						color: $colorWhite;
					}
				}
			}
		}
	}
	&-section{
		position: relative;
		display: flex;
		align-items: flex-start;
		margin-top: 100px;
	}
		&-filter{
			position: relative;
			width: 270px;
			margin-bottom: 50px;
			margin-right: 90px;
			&__inner{
				position: relative;
			}
			&__title{
				position: relative;
				color: $colorDarkGrey;
				font-size: 16px;
				font-weight: 700;
				letter-spacing: 0.0625em;
				text-transform: uppercase;
				margin-bottom: 20px;
			}
			&__head{
				position: relative;
				margin-bottom: 20px;
			}
				&__result{
					position: relative;
					display: flex;
					flex-wrap: wrap;
				}
					&__tag{
						position: relative;
						background: $colorLightBg;
						max-width: 100%;
						color: $colorDarkGrey;
						font-size: 12px;
						line-height: 2.15;
						padding: 5px 30px 5px 15px;
						margin-bottom: 10px;
						&:not(:last-child){
							margin-right: 10px;
						}
						&-title{
							position: relative;
							max-width: 100%;
							text-overflow: ellipsis;
							white-space: nowrap;
							overflow: hidden;
						}
						&-del{
							position: absolute;
							display: flex;
							align-items: center;
							justify-content: center;
							top: 0;
							right: 0;
							width: 30px;
							height: 100%;
							font-size: 8px;
							line-height: 0;
							text-align: center;
							transition: color 0.3s ease;
							cursor: pointer;
							&:hover{
								color: $colorGrey;
							}
						}
					}
				&__reset{
					position: relative;
					[type="reset"]{
						background: transparent;
						color: $colorDarkGrey;
						font-size: 12px;
						letter-spacing: 0.0825em;
						line-height: 2.15;
						text-decoration: underline;
						border: 0;
						transition: color 0.3s ease;
						&:hover{
							color: $colorText;
							text-decoration: none;
						}
					}
				}
			&__list{
				position: relative;
				border-top: 1px solid $colorBg;
			}
				&__box{
					position: relative;
					border-bottom: 1px solid $colorBg;
					&.show{
						.catalog-filter__box{
							&-head{
								.icon{
									transform: rotateX(180deg);
								}
							}
						}
					}
					&-head{
						position: relative;
						color: $colorBlack;
						font-size: 12px;
						font-weight: 700;
						letter-spacing: 0.07em;
						text-transform: uppercase;
						padding: 20px 0;
						cursor: pointer;
						.icon{
							position: absolute;
							top: 50%;
							right: 0;
							font-size: 5px;
							margin-top: -3px;
						}
					}
					&-body{
						position: relative;
						display: none;
						padding: 5px 0 20px;
						ul{
							li{
								position: relative;
								&:not(:last-child){
									margin-bottom: 20px;
								}
								&.active{
									a,
									span{
										color: $colorDarkGrey;
										font-weight: 700;
									}
								}
								b{
									font-weight: 400;
								}
								a{
									color: $colorLightGrey;
									transition: color 0.3s ease;
									&:hover{
										color: $colorGrey;
									}
								}
							}
						}
					}
						&-title{
							position: relative;
						}
				}
			&__toggle{
				position: relative;
				.btn{
					width: 50px;
					font-size: 0;
					font-weight: 500;
					padding: 0 10px;
					.icon{
						top: -1px;
						font-size: 16px;
					}
				}
			}
			&__close{
				position: absolute;
				display: none;
				top: 30px;
				right: 30px;
				color: $colorBlack;
				font-size: 18px;
				line-height: 0;
				cursor: pointer;
				z-index: 1;
			}
		}
		&-content{
			position: relative;
			flex-grow: 1;
			flex-shrink: 0;
			flex-basis: 0;
		}
			&-sort{
				position: relative;
				margin-bottom: 50px;
				&__box{
					position: relative;
					display: flex;
					align-items: center;
					color: $colorDarkGrey;
					font-size: 12px;
					line-height: 2.15;
					&:not(:first-child){
						margin-left: 80px;
					}
					&.sort-type{
						.select2{
							min-width: 140px;
							width: 150px !important;
						}
					}
					&-title{
						position: relative;
						margin-right: 10px;
					}
					&-body{
						position: relative;
					}
				}
				&__grid{
					position: relative;
					ul{
						display: flex;
						align-items: center;
						li{
							position: relative;
							background: $colorLightBg;
							display: flex;
							align-items: center;
							justify-content: center;
							width: 26px;
							height: 26px;
							color: rgba($colorDarkGrey, 0.7);
							line-height: 26px;
							text-align: center;
							border: 1px solid $colorLightBg;
							transition: all 0.3s ease;
							cursor: pointer;
							&:hover{
								color: $colorDarkGrey;
							}
							&.active{
								color: $colorDarkGrey;
								border-color: $colorBg;
							}
							&:not(:last-child){
								margin-right: 10px;
							}
						}
					}
				}
				.col-12{
					.catalog-filter__toggle{
						.btn{
							width: 100%;
							font-size: 14px;
							.icon{
								margin-right: 10px;
							}
						}
					}
				}
			}
			&-list{
				position: relative;
				&:not(:first-child){
					border-top: 1px solid $colorBg;
					padding-top: 50px;
				}
			}
	&-collection{
		position: relative;
		padding-top: 50px;
		&__box{
			position: relative;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			padding-top: 130.525%;
			margin-bottom: 50px;
			&:hover{
				.catalog-collection__box{
					&-title,
					&-link{
						visibility: visible;
						opacity: 1;
					}
				}
			}
			&-img{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				visibility: hidden;
				opacity: 0;
			}
			&-title{
				position: absolute;
				background: rgba($colorWhite, 0.8);
				top: 0;
				left: 0;
				right: 0;
				color: $colorDarkGrey;
				font-size: 44px;
				font-family: 'Playfair Display', sans-serif;
				letter-spacing: 1px;
				text-align: center;
				text-transform: uppercase;
				padding: 20px 30px;
				transition: all 0.3s ease;
				visibility: hidden;
				opacity: 0;
			}
			&-link{
				position: absolute;
				left: 50%;
				bottom: 50px;
				transform: translateX(-50%);
				transition: all 0.3s ease;
				visibility: hidden;
				opacity: 0;
				.btn{
