			flex-basis: 0;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 0.0625em;
			text-transform: uppercase;
		}
		&-list{
			width: 270px;
		}
		&-result{
			width: 130px;
			font-size: 20px;
			font-weight: 700;
			letter-spacing: 1px;
			text-align: center;
			text-transform: uppercase;
		}
	&__content{
		position: relative;
		display: flex;
		align-items: flex-start;
		margin-bottom: -50px;
		&:not(:first-child){
			margin-top: 100px;
		}
		&-img{
			position: relative;
			width: calc(50% - 15px);
			padding-left: 30px;
			margin-right: 30px;
			img{
				width: 205px;
			}
			ul{
				li{
					position: absolute;
					display: flex;
					align-items: center;
					justify-content: center;
					left: 295px;
					width: 30px;
					height: 30px;
					color: $colorDarkGrey;
					font-size: 16px;
					font-weight: 700;
					text-align: center;
					border-radius: 50%;
					border: 1px solid $colorBg;
					&:before{
						content: '';
						position: absolute;
						background: $colorBg;
						top: 50%;
						right: 100%;
						width: 265px;
						height: 1px;
						margin-right: 30px;
					}
					&:nth-child(1){
						top: 180px;
					}
					&:nth-child(2){
						top: 215px;
					}
					&:nth-child(3){
						top: 375px;
					}
				}
			}
		}
		&-text{
			position: relative;
			flex-grow: 1;
			flex-shrink: 0;
			flex-basis: 0;
			font-size: 14px;
			line-height: 1.85;
			margin-bottom: 50px;
			&:not(:first-child){
				padding-top: 30px;
			}
			h3{
				color: $colorDarkGrey;
				font-size: 16px;
				line-height: 1.25;
				text-transform: uppercase;
				letter-spacing: 0.0625em;
				margin-bottom: 30px;
			}
			p{
				&:not(:last-child){
					margin-bottom: 30px;
				}
			}
		}
	}
}
.wishlist{
	position: relative;
	max-width: 1470px;
	margin: 100px auto 0;
	&-list{
		position: relative;
	}
	&-share{
		position: relative;
		margin: 50px 0;
		&__title{
			position: relative;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 0.0625em;
			text-transform: uppercase;
			margin-bottom: 30px;
		}
		&__method{
			position: relative;
			display: flex;
			&-socials{
				position: relative;
				display: flex;
				&:not(:last-child){
					margin-right: 30px;
				}
				a{
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;
					width: 56px;
					height: 56px;
					color: #D4D4D4;
					font-size: 24px;
					text-align: center;
					border: 1px solid #D4D4D4;
					transition: color 0.3s ease;
					&:hover{
						.icon{
							transition: color 0.3s ease;
							&[class*="facebook"] {
								color: #2789F5;
							}
							&[class*="twitter"] {
								color: #27BAF5;
							}
							&[class*="pinterest"] {
								color: #F14336;
							}
						}
					}
				}
			}
			&-form{
				position: relative;
				input{
					width: 350px;
				}
				.btn{
					width: 210px;
				}
			}
		}
	}
}
.checkout{
	position: relative;
	max-width: 1170px;
	padding-top: 10px;
	margin: 0 auto 50px;
	&-count{
		position: relative;
		color: $colorDarkGrey;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 0.0625em;
		text-transform: uppercase;
	}
	&-list{
		position: relative;
		max-height: 690px;
		overflow-y: auto;
		&::-webkit-scrollbar{
			background: $colorBg;
			width: 4px;
			padding: 0;
			margin: 0;
		}
		&::-webkit-scrollbar-button{
			display: none;
		}
		&::-webkit-scrollbar-thumb{
			background: $colorBlack;
		}
		&__product{
			position: relative;
			display: flex;
			&:not(:last-child){
				margin-bottom: 30px;
			}
			&-head{
				position: relative;
				width: 210px;
				margin-right: 30px;
			}
				&-img{
					position: relative;
					display: block;
					line-height: 0;
				}
			&-body{
				position: relative;
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
				color: $colorDarkGrey;
				font-size: 14px;
				line-height: 1.35;
			}
				&-price{
					position: relative;
					font-weight: 700;
					letter-spacing: 0.5px;
					text-transform: uppercase;
					margin-bottom: 15px;
				}
				&-title{
					position: relative;
					letter-spacing: 0.07em;
					padding-right: 50px;
					margin-bottom: 15px;
				}
				&-options{
					position: relative;
					display: flex;
					flex-wrap: wrap;
					margin-bottom: 10px;
					span{
						margin-bottom: 5px;
						&:not(:last-child){
							margin-right: 20px;
						}
					}
				}
				&-count{
					position: relative;
					margin-bottom: 15px;
				}
				&-calc{
					position: relative;
					line-height: 1.85;
					margin-bottom: 15px;
				}
				&-total{
					position: relative;
					font-size: 16px;
					font-weight: 700;
					letter-spacing: 0.0625em;
				}
				&-delete{
					position: absolute;
					top: 0;
					right: 0;
					.btn{
						&.btn-icon{
							background: $colorLightBg;
							width: 48px;
							height: 48px;
							color: $colorBlack;
							border-color: $colorLightBg;
							padding: 0;
							//&:hover{
							//	background: $colorBlack;
							//	color: $colorWhite;
							//	border-color: $colorWhite;;
							//}
						}
					}
				}
		}
	}
	&-button{
		position: relative;
	}
	.check-lg{
		[type="checkbox"],
		[type="radio"]{
			& ~ label{
				opacity: 1;
			}
		}
	}
	.btn{
		width: 100%;
		padding: 0 15px;
	}
}
.price{
	&-row{
		position: relative;
		display: flex;
		&:not(:last-child){
			margin-bottom: 30px;
		}
	}
		&-col{
			position: relative;
			color: $colorDarkGrey;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 0.0625em;
			text-transform: uppercase;
			&:first-child{
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
			}
			&:last-child{
				width: 120px;
				text-align: right;
			}
			&:not(:last-child){
				margin-right: 30px;
			}
		}
}
.size{
	position: relative;
	padding-top: 10px;
	&-tabs{
		position: relative;
		margin: 0 -60px 50px;
		ul{
			display: flex;
			li{
				position: relative;
				background: $colorLightBg;
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
				color: $colorBlack;
				font-size: 16px;
				line-height: 1.5;
				letter-spacing: 0.125em;
				text-align: center;
				text-transform: uppercase;
				padding: 16px 15px;
				transition: all 0.3s ease;
				cursor: pointer;
				&:hover{
					color: $colorGrey;
				}
				&.active{
					background: $colorBlack;
					color: $colorWhite;
				}
			}
		}
	}
	&-list{
		position: relative;
		&__box{
			position: relative;
			display: none;
			&.show{
				display: block;
			}
		}
	}
		&-category{
			position: relative;
			&__tabs{
				position: relative;
				margin-bottom: 50px;
				ul{
					display: flex;
					justify-content: space-between;
					li{
						position: relative;
						&:not(:last-child){
							margin-right: 15px;
						}
					}
				}
			}
			&__list{
				position: relative;
			}
				&__box{
					position: relative;
					display: none;
					&.show{
						display: block;
					}
				}
			&__details{
				position: relative;
				display: flex;
				color: $colorBlack;
				&:not(:last-child){
					margin-bottom: 50px;
				}
				&-title{
					position: relative;
					width: 340px;
					font-size: 14px;
					font-weight: 700;
					letter-spacing: 0.0715em;
					text-transform: uppercase;
					margin-right: 30px;
				}
				&-list{
					position: relative;
					flex-grow: 1;
					flex-shrink: 0;
					flex-basis: 0;
					font-size: 12px;
					letter-spacing: 0.0825em;
					ul{
						column-count: 3;
						li{
							display: inline-block;
							vertical-align: top;
							width: 100%;
							margin-bottom: 10px;
						}
					}
				}
			}
		}
	.checkbox{
		[type="checkbox"],
		[type="radio"]{
			& ~ label{
				font-size: 14px;
				font-weight: 700;
				text-transform: uppercase;
				opacity: 1;
			}
		}
	}
	.table-responsive{
		&:not(:last-child){
			margin-bottom: 50px;
		}
	}
	table{
		width: 100%;
		min-width: 400px;
		&.table{
			&-lg{
				min-width: 680px;
			}
			&-sm{
				min-width: 580px;
			}
		}
		thead{
			tr{
				td,
				th{
					background: $colorBlack;
					color: $colorWhite;
					text-transform: uppercase;
					&[colspan]{
						text-align: left;
					}
				}
			}
		}
		tbody{
			tr{
				&:last-child{
					td,
					th{
						border-color: $colorLightBg;
					}
				}
				td,
				th{
					background: $colorDarkBg;
					border-bottom: 1px solid $colorBg;
					&[rowspan]{
						background: #fff;
						text-transform: none;
						border-left: 1px solid $colorBg;
						border-right: 1px solid $colorBg;
						b,
						strong{
							display: block;
							text-transform: uppercase;
						}
					}
				}
			}
		}
		tr{
			td,
			th{
				vertical-align: middle;
				height: 46px;
				color: $colorBlack;
				font-size: 12px;
				text-align: center;
				text-transform: uppercase;
				letter-spacing: 0.0825em;
				padding: 5px 15px;
			}
		}
	}
}
