$colorBlack: #000000;
$colorWhite: #ffffff;
$colorBg: #D4D4D4;
$colorLightBg: #FBFBFB;
$colorDarkBg: #F5F5F5;
$colorGrey: #454546;
$colorLightGrey: #5B5B5C;
$colorDarkGrey: #181819;
$colorText: #747475;
$colorLigthText: #8B8B8C;
$colorGold: #DFBB90;
$colorGreen: #00C48C;
$colorLightGreen: #99c71f;
$colorDarkGreen: #648300;
$colorRed: #F13807;
$colorLightRed: #ce7f7f;
$colorDarkRed: #881616;

html,
body{
	height: 100%;
}
body {
	background: $colorWhite;
	color: $colorGrey;
	font-size: 14px;
	font-family: 'Evolventa', sans-serif;
	line-height: 1.25;
	&.no-scroll{
		height: 100vh;
		overflow: hidden;
		-webkit-overflow-scrolling: touch;
	}
}
* {
	outline: none;
	box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6{
	font-size: 1em;
	font-weight: 700;
	line-height: 1;
	margin: 0;
}
img {
	vertical-align: top;
	max-width: 100%;
}
a{
	text-decoration: none;
	transition: none;
	&:hover{
		text-decoration: none;
	}
}
ul,
ol{
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}
p {
	margin: 0;
}
button{
	outline: none;
	&:focus{
		outline: none;
	}
}
[class^='icon-'],
[class*=' icon-']{
	position: relative;
	vertical-align: middle;
}
.wrapper {
	position: relative;
	min-width: 320px;
	min-height: 100%;
	padding-top: 184px;
	overflow: hidden;
}

.title{
	position: relative;
	margin-bottom: 20px;
}
.subtitle{
	position: relative;
	max-width: 860px;
	color: $colorLightGrey;
	letter-spacing: 0.0725em;
	line-height: 1.8;
	margin: 0 auto 40px;
}
h1, .h1{
	color: $colorDarkGrey;
	font-size: 44px;
	font-weight: 400;
	font-family: 'Playfair Display', sans-serif;
	line-height: 1;
	letter-spacing: 0.0225em;
	text-transform: uppercase;
}
h2, .h2{
	color: $colorDarkGrey;
	font-size: 20px;
	font-family: 'Evolventa', sans-serif;
	font-weight: 700;
	line-height: 1;
	letter-spacing: 1px;
	text-transform: uppercase;
}
.slick{
	&-arrow{
		position: absolute;
		background: transparent;
		top: 50%;
		width: 14px;
		height: 28px;
		font-size: 0;
		border: 0;
		padding: 0;
		transform: translateY(-50%);
		z-index: 10;
		&:before{
			content: '';
			position: absolute;
			display: flex;
			align-items: center;
			justify-content: center;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			color: $colorBlack;
			font-size: 28px;
			font-family: 'svgfont', sans-serif;
		}
	}
		&-prev{
			left: -15px;
			&:before{
				content: '\EA0D';
			}
		}
		&-next{
			right: -15px;
			&:before{
				content: '\EA0E';
			}
		}
	&-dots{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		li{
			position: relative;
			margin: 0 15px;
			&:only-of-type{
				display: none;
			}
			&.slick-active{
				button{
					background: $colorWhite;
					box-shadow: 0 0 0 2px $colorWhite;
				}
			}
			button{
				background: #AEAEAE;
				width: 10px;
				height: 10px;
				font-size: 0;
				border-radius: 50%;
				border: 0;
				padding: 0;
				margin: 0;
				transition: all 0.3s ease;
			}
		}
	}
}
.breadcrumbs{
	position: relative;
	background: $colorWhite;
	font-size: 0;
	border-bottom: 1px solid $colorBg;
	padding: 20px 0;
	ul{
		li{
			position: relative;
			display: inline;
			color: $colorText;
			font-size: 13px;
			letter-spacing: 0.08em;
			&:not(:last-child){
				&:after{
					content: '/';
					position: relative;
					margin: 0 5px;
				}
			}
			a{
				color: $colorText;
				transition: color 0.3s ease;
				&:hover{
					color: $colorLigthText;
				}
			}
		}
	}
}
.pagination{
	position: relative;
	display: block;
	border-radius: 0;
	padding: 50px 0;
	border-top: 1px solid $colorBg;
	&-title{
		position: relative;
		color: $colorDarkGrey;
		font-size: 12px;
		&:not(:last-child){
			margin-right: 5px;
		}
	}
	ul{
		display: flex;
		align-items: center;
		li{
			a{
				cursor: pointer;
				&:hover{
					opacity: 1;
				}
			}
			a,
			span:not(.icon){
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 26px;
				height: 26px;
				color: $colorDarkGrey;
				font-size: 12px;
				text-align: center;
				border: 1px solid transparent;
				transition: all 0.3s ease;
				opacity: 0.7;
				&.prev-page,
				&.next-page{
					color: $colorBlack;
					font-size: 8px;
					&:hover{
						color: $colorGrey;
					}
				}
				&.prev-page{
					margin-right: 10px;
				}
				&.next-page{
					margin-left: 10px;
				}
				&.disabled{
					color: $colorBg;
					pointer-events: none;
				}
			}
			a.active,
			span:not(.icon){
				background: $colorLightBg;
				border-color: $colorBg;
				opacity: 1;
			}
		}
	}
}
.share{
	position: relative;
	display: flex;
	align-items: center;
	a{
		color: $colorBg;
		font-size: 24px;
		line-height: 0;
		transition: color 0.3s ease;
		&:not(:last-child){
			margin-right: 30px;
		}
		&:hover{
			.icon{
				&[class*="facebook"]{
					color: #2789F5;
				}
				&[class*="twitter"]{
					color: #27BAF5;
				}
				&[class*="pinterest"]{
					color: #F14336;
				}
			}
		}
		.icon{
			transition: color 0.3s ease;
		}
	}
}
.range {
	&-box {
		position: relative;
		&__value{
			display: flex;
			align-items: center;
			justify-content: space-between;
			color: $colorLightGrey;
			font-size: 12px;
			padding-top: 20px;
			input{
				height: 38px;
				font-size: 15px;
				border: 2px solid $colorBlack;
				border-radius: 4px;
				box-shadow: none;
				padding: 0 10px;
			}
		}
	}
}
.irs {
	height: 10px;
	&-line {
		top: 4px!important;
		background: $colorBg!important;
		height: 2px!important;
		border-radius: 0!important;
	}
	&-max,
	&-min {
		display: none!important;
	}
	&-from,
	&-single,
	&-to {
		display: none!important;
	}
	&-bar {
		background: $colorBlack!important;
		top: 4px!important;
		height: 2px!important;
	}
	&-handle{
		top: 0!important;
		background: $colorBlack!important;
		width: 10px!important;
		height: 10px!important;
		border-radius: 50%!important;
		box-shadow: none!important;
		i{
			display: none!important;
		}
	}
}
.rating{
	position: relative;
	display: inline-flex;
	align-items: center;
	line-height: 1;
	&.js-rating{
		flex-direction: row-reverse;
		&:hover{
			[type="checkbox"]{
				& ~ label{
					.icon{
						color: $colorBg;
					}
				}
			}
		}
	}
	&-box{
		position: relative;
		&:not(:last-child){
			margin-left: 2px;
		}
		&:hover{
			& ~ .rating-box{
				[type="checkbox"]{
					& ~ label{
						.icon{
							color: $colorBlack;
						}
					}
				}
			}
			[type="checkbox"]{
				& ~ label{
					.icon{
						color: $colorBlack!important;
					}
				}
			}
		}
		[type="checkbox"]{
			position: absolute;
			left: -9999px;
			width: inherit;
			height: inherit;
			& ~ label{
				position: relative;
				display: block;
				font-family: 'svgfont', sans-serif;
				margin: 0;
				cursor: pointer;
				.icon{
					transition: color 0.3s ease;
				}
			}
			&:checked{
				& ~ label{
					.icon{
						color: $colorBlack;
					}
				}
			}
		}
	}
	.icon{
		vertical-align: top;
		color: $colorBg;
		font-size: 12px;
		&:not(:last-child){
			margin-right: 2px;
		}
		&.active{
			color: $colorBlack;
		}
	}
}
.select2{
	&-container{
		vertical-align: top;
		&--default{
			&.select2-container--open{
				.select2-selection{
					&--single{
						.select2-selection{
							&__arrow{
								b{
									transform: rotateX(180deg);
								}
							}
						}
					}
				}
			}
			.select2-selection{
				&--single{
					background: $colorWhite;
					height: 56px;
					border-radius: 0;
					border: 1px solid $colorBg;
					&.select-xs{
						height: 26px;
						.select2-selection{
							&__rendered{
								font-size: 12px;
								line-height: 26px;
								padding-right: 30px;
							}
							&__arrow{
								width: 30px;
								height: 26px;
							}
						}
					}
					.select2-selection{
						&__rendered{
							color: $colorGrey;
							font-size: 14px;
							line-height: 56px;
							padding-left: 15px;
							padding-right: 40px;
						}
						&__arrow{
							top: 0;
							right: 0;
							width: 40px;
							height: 56px;
							b{
								top: 0;
								left: 0;
								right: 0;
								bottom: 0;
								width: auto;
								height: auto;
								border: 0;
								margin: 0;
								&:before{
									content: '\EA07';
									position: absolute;
									top: 50%;
									left: 50%;
									font-size: 5px;
									font-family: 'svgfont', sans-serif;
									line-height: 1;
									transform: translate(-50%, -50%);
								}
							}
						}
					}
				}
			}
			.select2-results{
				&__options{
					&::-webkit-scrollbar{
						background: $colorBg;
						width: 4px;
						padding: 0;
						margin: 0;
					}
					&::-webkit-scrollbar-button{
						display: none;
					}
					&::-webkit-scrollbar-thumb{
						background: $colorBlack;
					}
				}
				&__option{
					color: $colorGrey;
					font-size: 14px;
					line-height: 1.85;
					padding: 15px 15px;
					transition: all 0.3s ease;
					&:nth-child(odd){
						background: $colorLightBg;
					}
					&[aria-selected=true]{
						background: $colorBlack;
						color: $colorWhite;
					}
					&--highlighted[aria-selected]{
						background: $colorBg;
						color: $colorBlack;
					}
				}
			}
		}
	}
	&-dropdown{
		background: $colorWhite;
		border-radius: 0;
		border: 1px solid $colorBg;
		z-index: 49;
		&.select-xs-dropdown{
			.select2-results{
				&__option{
					font-size: 12px;
					padding: 2px 15px;
				}
			}
		}
	}
}
.back{
	position: relative;
	margin-bottom: 25px;
	a{
		color: $colorText;
		transition: color 0.3s ease;
		&:hover{
			color: $colorLigthText;
		}
		.icon{
			top: -3px;
			font-size: 8px;
			margin-right: 20px;
		}
	}
}
.count{
	position: relative;
	&-box{
		position: relative;
		display: flex;
		height: 56px;
		border: 1px solid $colorBg;
		&.count-xs{
			height: 26px;
			width: 134px;
			.count{
				&-inc,
				&-dec{
					width: 44px;
				}
				&-field{
					input{
						height: 24px;
					}
				}
			}
		}
	}
	&-inc,
	&-dec{
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		color: $colorDarkGrey;
		text-align: center;
		cursor: pointer;
		&::selection{
			background: none;
		}
		&.disabled{
			pointer-events: none;
		}
	}
	&-inc{
		border-left: 1px solid $colorBg;
	}
	&-dec{
		border-right: 1px solid $colorBg;
	}
	&-field{
		position: relative;
		flex-grow: 1;
		flex-shrink: 0;
		flex-basis: 0;
		overflow: hidden;
		input{
			text-align: center;
			height: 54px;
			border: 0;
			padding: 0 10px;
		}
	}
}
.bg{
	&-grey{
		background: $colorLightBg;
	}
}
.modal{
	&-content{
		background: $colorWhite;
		border-radius: 0;
		border: 1px solid #979797;
	}
	&-header{
		position: relative;
		color: $colorDarkGrey;
		font-size: 16px;
		font-weight: 700;
		line-height: 1.5;
		letter-spacing: 0.0625em;
		text-transform: uppercase;
		padding: 30px;
	}
	&-body{
		padding: 50px 60px;
	}
		&-title{
			position: relative;
			color: $colorDarkGrey;
			font-size: 44px;
			font-weight: 400;
			font-family: 'Playfair Display', sans-serif;
			letter-spacing: 0.0225em;
			text-align: center;
			text-transform: uppercase;
			margin-bottom: 40px;
			& + .subtitle{
				margin-top: -20px;
			}
		}
	&-form{
		position: relative;
		.form{
			&-group{
				.file-box{
					&__field{
						text-align: center;
					}
					&__list{
						&-box{
							justify-content: center;
						}
					}
				}
			}
			&-flex{
				display: flex;
				align-items: flex-start;
			}
			&-button{
				position: relative;
				text-align: center;
				padding-top: 30px;
				.btn{
					width: 250px;
				}
			}
		}
	}
	&-close{
		position: absolute;
		top: 30px;
		right: 30px;
		.close{
			color: $colorBlack;
			font-size: 18px;
			opacity: 1;
			transition: color 0.3s ease;
			&:hover{
				color: $colorDarkGrey;
				opacity: 1 !important;
			}
		}
	}
	textarea{
		height: 200px;
	}
}
#modal-forgot{
	.modal{
		&-body{
			padding-left: 30px;
			padding-right: 30px;
		}
	}
}
#modal-size{
	.modal-dialog{
		margin: 0;
	}
	&.fade{
		.modal-dialog{
			transform: translate(-50px, 0);
		}
	}
	&.show{
		.modal-dialog{
			transform: none;
		}
	}
}
.autocomplete{
	&-suggestions{
		background: $colorWhite;
		border: 1px solid $colorBg;
		margin-top: -1px;
		overflow-y: auto;
		&::-webkit-scrollbar{
			background: $colorBg;
			width: 4px;
			padding: 0;
			margin: 0;
		}
		&::-webkit-scrollbar-button{
			display: none;
		}
		&::-webkit-scrollbar-thumb{
			background: $colorBlack;
		}
	}
	&-suggestion{
		position: relative;
		color: $colorGrey;
		font-size: 14px;
		line-height: 1.85;
		padding: 15px 15px;
		transition: all 0.3s ease;
		cursor: pointer;
		&:nth-child(odd){
			background: $colorLightBg;
		}
		&:hover{
			background: $colorBg;
			color: $colorBlack;
		}
		b,
		strong{
			color: $colorBlack;
		}
	}
}
.mfilter-content-wrapper{
	overflow: hidden;
}
