.header{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	&.show{
		position: fixed;
		.header{
			&-top{

			}
			&-middle{
				display: none;
			}
			&-bottom{
				position: absolute;
				top: 100%;
				left: 0;
				right: 0;
				opacity: 0;
				visibility: hidden;
				&.show{
					opacity: 1;
					visibility: visible;
					transition: all 0.3s ease;
				}
			}
		}
	}
	&-top{
		position: relative;
		background: $colorBlack;
		height: 54px;
		z-index: 2;
	}
	&-middle{
		position: relative;
		background: $colorWhite;
		height: 74px;
		.row{
			height: 74px;
		}
	}
	&-bottom{
		position: relative;
		background: $colorWhite;
		height: 56px;
		border-bottom: 1px solid $colorBg;
	}
	&-account,
	&-wishlist,
	&-basket{
		position: relative;
		&.show{
			.header{
				&-account,
				&-wishlist,
				&-basket{
					&__modal{
						visibility: visible;
						opacity: 1;
					}
				}
			}
		}
		&:not(:first-child){
			margin-left: 90px;
		}
		&:before{
			content: '';
			position: absolute;
			background: $colorWhite;
			top: 0;
			left: 50%;
			width: 0;
			height: 3px;
			transform: translateX(-50%);
			transition: all 0.3s ease;
		}
		&:hover{
			&:before{
				width: 100%;
			}
		}
		&__title{
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			height: 54px;
			color: $colorWhite;
			font-size: 12px;
			letter-spacing: 0.0825em;
			text-align: center;
			text-transform: uppercase;
			cursor: pointer;
			&:hover{
				color: $colorWhite;
			}
			.icon{
				top: -2px;
				font-size: 18px;
				margin-right: 10px;
			}
			b{
				font-weight: 400;
				margin-left: 5px;
			}
		}
	}
	&-modals{
		position: absolute;
		top: 54px;
		left: 0;
		right: 0;
		&__list{
			position: relative;
		}
	}
	&-modal{
		position: absolute;
		background: $colorWhite;
		top: 0;
		right: 0;
		width: 375px;
		border: 1px solid $colorBg;
		transition: all 0.3s ease;
		//visibility: hidden;
		//opacity: 0;
		z-index: 20;
		&__list{
			position: relative;
			&-product{
				position: relative;
				display: flex;
				color: $colorDarkGrey;
				border-bottom: 1px solid $colorBg;
				&:first-child{
					.header{
						&-modal{
							&__list{
								&-product{
									&-head{
										padding-top: 50px;
									}
									&-body{
										padding-top: 50px;
									}
								}
							}
						}
					}
				}
				&-head{
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;
					width: 90px;
					text-align: center;
					border-right: 1px solid $colorBg;
					padding: 20px 15px;
					.icon{
						top: -2px;
						font-size: 28px;
					}
				}
				&-body{
					position: relative;
					display: flex;
					flex-direction: column;
					justify-content: center;
					flex-grow: 1;
					flex-shrink: 0;
					flex-basis: 0;
					font-size: 14px;
					line-height: 1.85;
					padding: 30px;
				}
					&-title{
						position: relative;
						text-transform: uppercase;
					}
					&-text{
						position: relative;
					}
			}
			&-link{
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 30px;
				.btn{
					width: 250px;
					font-size: 14px;
				}
			}
		}
		&__close{
			position: absolute;
			top: 20px;
			right: 30px;
			color: $colorBlack;
			font-size: 18px;
			transition: color 0.3s ease;
			z-index: 1;
			cursor: pointer;
			&:hover{
				color: $colorDarkGrey;
			}
		}
	}
}
.brand{
	position: relative;
	display: flex;
	&-title{
		position: relative;
		display: flex;
		align-items: center;
		width: 150px;
		color: $colorWhite;
		font-size: 12px;
		letter-spacing: 0.0425em;
	}
	&-list{
		ul{
			position: relative;
			display: flex;
			li{
				position: relative;
				&:not(:last-child){
					margin-right: -1px;
					&:before{
						content: '';
						position: absolute;
						background: $colorWhite;
						top: 50%;
						right: 0;
						width: 1px;
						height: 24px;
						transform: translateY(-50%);
					}
				}
				a{
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;
					height: 54px;
					color: $colorWhite;
					letter-spacing: 0.0625em;
					text-align: center;
					text-transform: uppercase;
					padding: 10px 30px;
					&:before{
						content: '';
						position: absolute;
						background: $colorWhite;
						top: 0;
						left: 50%;
						height: 3px;
						width: 0;
						transform: translateX(-50%);
						transition: all 0.3s ease;
					}
					&:hover{
						&:before{
							width: 100%;
						}
					}
					&.active{
						background: $colorWhite;
						color: $colorBlack;
						img{
							filter: invert(0);
						}
					}
					img{
						display: none;
						filter: invert(100%);
					}
				}
			}
		}
	}
}
.menu-toggle{
	position: relative;
	font-size: 24px;
	line-height: 0;
	margin-right: 90px;
	cursor: pointer;
	transition: color 0.3s ease;
	&:hover{
