	&-arr{
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		top: 50%;
		right: 0;
		width: 20px;
		height: 20px;
		color: $colorBlack;
		font-size: 5px;
		text-align: center;
		cursor: pointer;
		transform: translateY(-50%);
		margin-top: -1px;
	}
	&-list{
		position: absolute;
		background: $colorWhite;
		top: 100%;
		left: -10px;
		border: 1px solid $colorBg;
		visibility: hidden;
		opacity: 0;
		transition: all 0.3s ease;
		z-index: 5;
		ul{
			li{
				&:not(:last-child){
					border-bottom: 1px solid $colorBg;
				}
				a{
					display: flex;
					align-items: center;
					height: 36px;
					color: $colorBlack;
					font-size: 13px;
					letter-spacing: 0.0775em;
					padding: 5px 10px;
					transition: color 0.3s ease;
					mix-blend-mode: normal;
					opacity: 0.95;
					&:hover{
						color: $colorGrey;
					}
				}
			}
		}
	}
}
