.search{
	position: relative;
	color: $colorBlack;
	font-size: 0;
	margin-left: 90px;
	cursor: pointer;
	transition: color 0.3s ease;
	&:hover{
		color: $colorDarkGrey;
	}
	.icon{
		font-size: 18px;
	}
	&-form{
		position: relative;
		display: none;
		&__box{
			position: relative;
			.form{
				&-froup{
					margin-bottom: 0;
				}
			}
			input{
				background: $colorLightBg;
				border: 0;
				padding-right: 40px;
			}
			button{
				position: absolute;
				background: transparent;
				display: flex;
				align-items: center;
				justify-content: center;
				top: 0;
				right: 0;
				width: 40px;
				height: 50px;
				color: $colorLigthText;
				font-size: 18px;
				border: 0;
				padding: 0;
				margin: 0;
				transition: color 0.3s ease;
				cursor: pointer;
				&[type="submit"]{
					opacity: 0.8;
				}
			}
		}
		&__modal{
			position: absolute;
			background: $colorWhite;
			top: 100%;
			height: 0;
			left: -15px;
			right: -15px;
			padding: 0 15px;
			transition: all 0.3s ease;
			overflow: hidden;
			z-index: 10;
			&.show{
				padding: 30px 15px;
			}
		}
			&__nothing{
				position: relative;
				&-text{
					position: relative;
					color: $colorLightGrey;
					font-size: 14px;
					letter-spacing: 0.0715em;
					text-align: center;
					margin-bottom: 20px;
				}
				&-count{
					position: relative;
					color: $colorDarkGrey;
					font-size: 14px;
					font-weight: 700;
					letter-spacing: 0.0625em;
					text-align: center;
					text-transform: uppercase;
				}
			}
			&__result{
				position: relative;
				max-height: 100%;
				overflow-y: auto;
			}
				&__list{
					position: relative;
				}
				&__link{
					position: relative;
					text-align: center;
					.btn{
						width: 250px;
						padding: 0 15px;
					}
				}
	}
}
