.modal-menu{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease;
	z-index: 10;
	&:before{
		content: '';
		position: absolute;
		background: $colorBlack;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0.3;
	}
	&.show{
		opacity: 1;
		visibility: visible;
		.modal-menu{
			&__box{
				left: 0;
			}
		}
	}
	&__box{
		position: absolute;
		background: $colorWhite;
		top: 0;
		left: -495px;
		width: 495px;
		height: 100%;
		padding: 90px 0;
		transition: all 0.3s ease;
		&-list{
			position: relative;
			max-height: 100%;
			padding: 0 115px 0 70px;
			overflow-x: hidden;
			overflow-y: auto;
			&::-webkit-scrollbar{
				background: $colorBg;
				width: 4px;
				padding: 0;
				margin: 0;
			}
			&::-webkit-scrollbar-button{
				display: none;
			}
			&::-webkit-scrollbar-thumb{
				background: $colorBlack;
			}
		}
	}
	&__close{
		position: absolute;
		top: 70px;
		right: 30px;
		color: $colorBlack;
		font-size: 18px;
		line-height: 0;
		cursor: pointer;
		z-index: 1;
	}
}
