.footer{
	position: sticky;
	position: relative;
	background: $colorWhite;
	top: 100%;
	border-top: 1px solid $colorBg;
	&-top{
		position: relative;
	}
	&-middle{
		position: relative;
		background: $colorLightBg;
		padding-top: 50px;
	}
	&-bottom{
		position: relative;
		display: flex;
		align-items: center;
		background: $colorBlack;
		height: 50px;
		color: $colorWhite;
		padding: 15px 0;
	}
	&-inner{
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
		&-group{
			position: relative;
			display: flex;
			&:before{
				content: '';
				position: absolute;
				background: $colorBg;
				top: 0;
				left: 50%;
				width: 1px;
				height: 100%;
				margin-left: -0.5px;
			}
			&__box{
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: center;
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
				height: 380px;
				&:not(:first-child){
					//border-left: 1px solid $colorBg;
				}
				&:nth-child(odd){
					padding-right: 90px;
				}
				&:nth-child(even){
					padding-left: 90px;
				}
				&-title{
					position: relative;
					color: $colorDarkGrey;
					font-size: 16px;
					font-weight: 700;
					letter-spacing: 0.0625em;
					text-transform: uppercase;
					margin-bottom: 20px;
				}
				&-text{
					position: relative;
					max-width: 420px;
					line-height: 1.9;
					margin-bottom: 30px;
				}
			}
		}
		&-box{
			position: relative;
			display: flex;
			flex-direction: column;
			max-width: 360px;
			&:not(:last-child){
				margin-bottom: 40px;
			}
			&__head{
				position: relative;
				margin-bottom: 30px;
			}
				&__title{
					position: relative;
					color: $colorBlack;
					font-weight: 700;
					letter-spacing: 0.0725em;
					text-transform: uppercase;
					a{
						color: $colorBlack;
						transition: color 0.3s ease;
						&:hover{
							color: $colorDarkGrey;
						}
					}
				}
				&__arr{
					position: absolute;
					display: none;
					align-items: center;
					justify-content: center;
					top: 0;
					right: 0;
					width: 60px;
					height: 100%;
					line-height: 0;
					cursor: pointer;
					z-index: 1;
					.icon{
						color: $colorBlack;
						font-size: 5px;
					}
				}
			&__body{
				position: relative;
				//display: flex;
				//flex-direction: column;
				//width: 100%;
				//flex-grow: 1;
				//flex-shrink: 0;
				//flex-basis: 0;
				//justify-content: space-between;
				ul{
					position: relative;
					&:not(:last-child){
						margin-bottom: 30px;
					}
					li{
						position: relative;
						&:not(:last-child){
							margin-bottom: 20px;
						}
						a{
							color: $colorGrey;
							font-size: 13px;
							line-height: 1.45;
							letter-spacing: 0.0725em;
							transition: color 0.3s ease;
							&:hover{
								color: $colorText;
							}
						}
					}
				}
			}
		}
		&-menu{
			position: relative;
			width: 100%;
			max-width: none;
			padding: 30px 0;
			&:before{
				content: '';
				position: absolute;
				background: $colorBg;
				top: 0;
				left: 50%;
				height: 1px;
				width: 100vw;
				transform: translateX(-50%);
			}
			.footer-box{
				&__head{
					display: none;
				}
				&__body{
					ul{
						display: flex;
						justify-content: space-between;
						li{
							&:not(:last-child){
								margin-bottom: 0;
							}
							a{
								color: $colorBlack;
								font-size: 14px;
								font-weight: 700;
								&:hover{
									color: $colorGrey;
								}
							}
						}
					}
				}
			}
		}
}
.subscribe{
	position: relative;
	&-form{
		display: flex;
		.form{
			&-group{
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
				margin-bottom: 0;
			}
			&-button{
				.btn{
					width: 210px;
				}
			}
		}
	}
}
.socials{
	position: relative;
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	a{
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		color: $colorBg;
		font-size: 24px;
		text-align: center;
		border: 1px solid $colorBg;
		transition: color 0.3s ease;
		&:hover{
			color: $colorText;
		}
		&:not(:last-child){
			margin-right: 20px;
		}
	}
}
.copyright{
	position: relative;
	font-size: 13px;
	letter-spacing: 0.075em;
	mix-blend-mode: normal;
	opacity: 0.8;
}
.develop{
	position: relative;
	&-modal{
		position: absolute;
		background: $colorWhite;
		right: 0;
		bottom: calc(100% + 10px);
		width: 160px;
		color: $colorGrey;
		font-size: 10px;
		line-height: 1.25;
		border: 1px solid $colorBg;
		padding: 5px 10px;
		transition: all 0.3s ease;
		visibility: hidden;
		opacity: 0;
	}
	a{
		display: block;
		line-height: 0;
		&:hover{
			.develop-modal{
				visibility: visible;
				opacity: 1;
			}
		}
	}
	img{
		width: 22px;
	}
}
.payment{
	position: relative;
	display: flex;
	align-items: center;
	a{
		&:not(:last-child){
			margin-right: 30px;
		}
	}
	img{
		max-width: 65px;
		max-height: 40px;
	}
}
