.btn {
	position: relative;
	display: inline-block;
	background: $colorDarkGrey;
	height: 56px;
	color: $colorWhite;
	font-size: 16px;
	font-weight: 400;
	font-family: 'Oswald', sans-serif;
	line-height: 54px;
	letter-spacing: 0.125em;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	border: 1px solid $colorDarkGrey;
	border-radius: 0;
	box-shadow: none;
	padding: 0 30px;
	transition: all 0.3s ease;
	overflow: hidden;
	outline: none;
	cursor: pointer;

	&:hover,
	&:active{
		background: transparent;
		color: $colorDarkGrey;
		text-decoration: none;
		outline: none;
	}
	&:focus{
		text-decoration: none;
		box-shadow: none;
		outline: none;
	}

	&-black{
		background: transparent;
    	color: $colorDarkGrey;
		&:hover,
		&:active{
			background: $colorBlack;
			color: $colorWhite;
		}
	}

	&-white{
		background: $colorWhite;
		color: $colorBlack;
		border-color: $colorWhite;
		&:hover,
		&:active{
			background: $colorBlack;
			color: $colorWhite;
			border-color: $colorBlack;
		}
	}

	&-gold{
		background: transparent;
		color: $colorGold;
		border-color: $colorGold;
		&:hover,
		&:active{
			background: $colorGold;
			color: $colorBlack;
		}
	}

	&-link{
		background: transparent;
		color: $colorDarkGrey;
		line-height: 56px;
		border: 0;
		&:hover,
		&:active{
			background: $colorBlack;
			color: $colorWhite;
		}
	}

	&-icon{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		font-size: 24px;
		letter-spacing: normal !important;
		line-height: 0 !important;
		border-radius: 50%;
		padding: 0;
		&.btn{
			&-lg{
				width: 50px;
				height: 50px;
				font-size: 20px;
			}
		}
	}

	&-lg{
		height: 50px;
		font-size: 12px;
		line-height: 48px;
	}


	&.disabled,
	&[disabled]{
		pointer-events: none;
		opacity: 1;
		cursor: auto;
	}
}
.link{
	position: relative;
	color: $colorDarkGrey;
	text-decoration: underline;
	&:hover{
		text-decoration: none;
	}
}
