@media screen and (min-width: 576px){
	.modal{
		&-dialog{
			max-width: 630px;
		}
	}
}

@media screen and (min-width: 768px){
	.modal{
		&-lg{
			max-width: 990px;
		}
	}
}

@media screen and (min-width: 1200px){
	.catalog-menu{
		& > ul{
			& > li{
				&:hover{
					.catalog-menu{
						&__modal{
							visibility: visible;
							opacity: 1;
						}
					}
				}
			}
		}
	}
	.catalog{
		&-list{
			.col{
				&-xl{
					&-2{
						flex: 0 0 25%;
						max-width: 25%;
						.product{
							&-action {
								.btn {
									&:not(.btn-icon) {
										width: 200px;
										height: 40px;
										line-height: 40px;
										padding: 0 15px;
									}
									&-icon{
										width: 40px;
										height: 40px;
									}
								}
							}
							//&-body{
							//	padding-left: 0;
							//	padding-right: 0;
							//}
						}
					}
					&-3{
						//flex: 0 0 33.333333%;
						//max-width: 33.333333%;
						.product{
							&-action {
								.btn {
									&:not(.btn-icon) {
										width: 200px;
										height: 40px;
										line-height: 40px;
										padding: 0 15px;
									}
									&-icon{
										width: 40px;
										height: 40px;
									}
								}
							}
						}
					}
					//&-4{
					//	flex: 0 0 50%;
					//	max-width: 50%;
					//}
				}
			}
		}
	}
}

@media screen and (min-width: 1800px){
	.d-xxl-block{
		display: block !important;
	}
	.catalog{
		&-list{
			.col{
				&-xl{
					&-2{
						flex: 0 0 16.666667%;
						max-width: 16.666667%;
						.product{
							&-action{
								padding-left: 5px;
								padding-right: 5px;
								.btn{
									&:not(.btn-icon){
										width: 180px;
										padding: 0 5px;
									}
								}
							}
						}
					}
					&-3{
						//flex: 0 0 25%;
						//max-width: 25%;
						.product{
							&-action{
								padding-left: 5px;
								padding-right: 5px;
								.btn{
									&:not(.btn-icon){
										width: 230px;
										height: 50px;
										line-height: 50px;
									}
									&-icon{
										width: 50px;
										height: 50px;
										font-size: 20px;
									}
								}
							}
						}
					}
					//&-4{
					//	flex: 0 0 33.333333%;
					//	max-width: 33.333333%;
					//}
				}
			}
		}
	}
}

@media screen and (min-width: 1680px){
	.blog{
		&-details{
			.col-xl-8{
				flex: 0 0 58.333333%;
				max-width: 58.333333%;
			}
		}
	}
	.faq{
		.col-xl-8{
			flex: 0 0 58.333333%;
			max-width: 58.333333%;
		}
	}
}

@media screen and (max-width: 1799px){
	.catalog{
		&-category{
			ul{
				li{
					a{
						width: 225px;
					}
				}
			}
		}
		&-filter{
			margin-right: 30px;
		}
		&-sort{
			&__box{
				&:not(:first-child){
					margin-left: 30px;
				}
			}
			//&__grid{
			//	ul{
			//		li{
			//			font-size: 0;
			//			&:before{
			//				content: attr(data-grid);
			//				position: relative;
			//				font-size: 12px;
			//			}
			//		}
			//	}
			//}
		}
		&-collection{
			&__details{
				&-poster{
					width: 485px;
					margin-right: 30px;
				}
				&-list{
					&:not(:first-child){
						width: 450px;
					}
				}
			}
		}
	}
	.wishlist{
		.col-lg-3{
			.product{
				&-action{
					.btn{
						&:not(.btn-icon){
							width: 200px;
						}
					}
				}
			}
		}
	}
	.count{
		&-box{
			&.count-xs{
				width: 120px;
				.count{
					&-inc,
					&-dec{
