	}
	.product{
		&-options{
			&__size{
				max-width: 144px;
			}
		}
	}
}

@media screen and (max-width: 1679px){
	.similars{
		&-list{
			&__box{
				&-slider{
					.slick{
						&-arrow{
							top: 147px;
						}
						&-prev{
							left: -15px;
						}
						&-next{
							right: -15px;
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1439px){
	h1, .h1{
		font-size: 40px;
	}
	.head{
		margin-bottom: 70px;
		&-slider{
			.slick{
				&-dots{
					bottom: 15px;
				}
			}
		}
	}
	.catalog-menu{
		&__modal{
			&-box{
				flex-shrink: 1;
				flex-basis: auto;
				min-width: 180px;
			}
		}
	}
	.hot{
		padding-bottom: 80px;
		&-box{
			&__body{
				padding-left: 0;
			}
			&__list{
				margin-bottom: 40px;
				&-box{
					width: calc(50% - 15px);
					&:not(:nth-last-child(-n+2)){
						margin-bottom: 30px;
					}
				}
			}
			&__title{
				&:not(:last-child){
					margin-bottom: 20px;
				}
			}
		}
	}
	.main-catalog{
		&-list{
			padding: 0 35px;
			&__box{
				&-title{
					font-size: 40px;
				}
			}
		}
	}
	.not-found{
		&__box{
			padding: 100px 0 150px;
		}
	}
	.product{
		&-card{
			&__gallery{
				&-big{
					width: calc(100% - 130px);
				}
				&-small{
					width: 100px;
				}
			}
		}
	}
	.modal{
		&-title{
			font-size: 40px;
		}
	}
	.catalog{
		&-collection{
			&__box{
				&-title{
					font-size: 40px;
				}
			}
		}
	}
}

@media screen and (max-width: 1319px){
	h1, .h1{
		font-size: 36px;
	}
	.main-catalog{
		&-list{
			&__box{
				&-title{
					font-size: 36px;
				}
			}
		}
	}
	.footer{
		&-group{
			&__box{
				&:nth-child(odd){
					padding-right: 60px;
				}
				&:nth-child(even){
					padding-left: 60px;
				}
			}
		}
	}
	.modal{
		&-title{
			font-size: 36px;
		}
	}
}
@media screen and (max-width: 1299px){
	.catalog{
		&-filter{
			width: 220px;
		}
		&-category{
			ul{
				li{
					a{
						width: 195px;
					}
				}
			}
		}
		&-collection{
