				}
			}
		}
	}
	.similars{
		&-list{
			&__box{
				&-slider{
					.slick{
						&-arrow{
							top: 127px;
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1199px){
	.wrapper{
		padding-top: 128px;
	}
	h1, .h1{
		font-size: 24px;
	}
	.btn{
		height: 50px;
		font-size: 14px;
		line-height: 48px;
	}
	input{
		height: 50px;
	}
	.form{
		&-group{
			&.d-flex{
				&:not(:first-child){
					margin-top: 40px;
				}
				&:not(:last-child){
					margin-bottom: 40px;
				}
				& > label{
					margin-right: 40px;
				}
			}
		}
	}
	.subtitle{
		margin-bottom: 30px;
	}
	.pagination{
		padding: 40px 0;
	}
	.back{
		margin-bottom: 20px;
	}
	.count{
		&-box{
			height: 50px;
		}
		&-inc,
		&-dec{
			width: 50px;
		}
		&-field{
			input{
				height: 48px;
			}
		}
	}
	.header{
		&.show{
			.header{
				&-middle{
					display: block;
				}
				&-bottom{
					opacity: 1;
					visibility: visible;
				}
			}
			.catalog-menu{
				max-height: calc(100vh - 110px);
			}
		}
		&-middle{
			border-bottom: 1px solid $colorBg;
		}
		&-bottom{
			display: none;
			background: $colorWhite;
			height: auto;
		}
		&-account,
		&-wishlist,
		&-basket{
			&:not(:first-child){
				margin-left: 50px;
			}
		}
	}
	.brand{
		margin-left: -30px;
		&-title{
			display: none;
		}
		&-list{
			ul{
				li{
					&:first-child{
						a{
							&.active{
								&:after{
									content: '';
									position: absolute;
									background: $colorWhite;
									top: 0;
									right: 100%;
									width: 50vw;
									height: 100%;
								}
							}
						}
					}
				}
			}
		}
	}
	.logo{
		img{
			max-width: 120px;
			max-height: 34px;
		}
	}
	.menu-toggle,
	.location{
		margin-right: 50px;
	}
	.lang,
	.search{
		margin-left: 50px;
	}
	.catalog-menu{
		//display: none;
		//left: 50%;
		//width: 100vw;
		//max-height: calc(100vh - 184px);
		//transform: translateX(-50%);
		//overflow-x: hidden;
		//overflow-y: auto;
		margin-bottom: 30px;
		&:before,
		&:after{
			content: '';
			position: absolute;
			background: $colorBg;
			left: 50%;
			width: 100vw;
			height: 1px;
			transform: translateX(-50%);
		}
		&:before{
			top: 0;
		}
		&:after{
			bottom: 0;
		}
		&__toggle{
			display: flex;
			&.show{
				&:before{
					content: '';
					position: absolute;
					background: $colorBg;
					bottom: 0;
					left: 50%;
					width: 100vw;
					height: 1px;
					transform: translateX(-50%);
				}
			}
		}
		&__arr{
			display: flex;
		}
		& > ul{
			flex-direction: column;
			align-items: center;
			& > li{
				position: relative;
				width: 100%;
				&:not(:first-child){
					&:before{
						content: '';
						position: absolute;
						background: $colorBg;
						top: 0;
						left: 50%;
						width: 100vw;
						height: 1px;
						transform: translateX(-50%);
					}
				}
				&.show{
					.catalog-menu__arr{
						.icon{
							transform: rotateX(180deg);
						}
					}
				}
				& > a{
					display: block;
					height: auto;
					padding: 20px 0;
					&:before{
						content: none;
					}
				}
			}
		}
		&__modal{
			position: relative;
			display: none;
			flex-direction: column;
			border: 0;
			transition: none;
			visibility: visible;
			opacity: 1;
			&-box{
				min-width: 0;
				padding: 0 1px 0 10px;
				margin-bottom: 10px;
				&:not(:first-child){
					border: 0;
				}
				&.show{
					.catalog-menu__modal-category-arr{
						.icon{
							transform: rotateX(180deg);
						}
					}
				}
			}
				&-category{
					padding: 10px 0;
					margin-bottom: 0;
					&-arr{
						display: flex;
						margin-right: -6px;
					}
				}
				&-list{
					display: none;
					padding: 10px 0;
				}
		}
	}
	.top-menu{
		padding-right: 16px;
	}
	.modal{
		&-menu{
			&__box{
				width: 430px;
				padding: 50px 0;
				&-list{
					padding: 0 20px 0 25px;
				}
			}
			&__close{
				top: 15px;
			}
		}
	}
	.section{
		padding-bottom: 60px;
	}
	.head{
		margin-bottom: 60px;
	}
	.hot{
		&-box{
			&__body{
				padding-right: 0;
			}
			&__list{
				padding: 0 30px;
			}
				&__title{
					&:not(:last-child) {
						margin-bottom: 10px;
					}
				}
				&__text{
					font-size: 14px;
					&:not(:last-child) {
						margin-bottom: 20px;
					}
				}
		}
	}
	.main-catalog{
		&-list{
			padding: 0 10px;
			&__box{
				&-title{
					font-size: 24px;
					margin-bottom: 20px;
				}
				&-link{
					display: none;
				}
			}
		}
	}
	.main{
		&-about{
			padding: 80px 0;
			&__list{
				padding-top: 30px;
			}
		}
	}
	.benefits{
		&-list{
			&__box{
				padding: 30px;
			}
		}
	}
	.footer{
		&-middle{
			padding-top: 0;
		}
		&-bottom{
			height: 60px;
		}
		&-group{
			&__box{
				justify-content: flex-start;
				height: auto;
				padding: 40px 0 80px;
				&:nth-child(odd){
					padding-right: 50px;
				}
				&:nth-child(even){
					padding-left: 50px;
				}
			}
		}
		&-box{
			width: 50%;
			max-width: none;
			&:after{
				content: '';
				position: absolute;
				background: $colorBg;
				top: 0;
				height: 1px;
				width: 100vw;
			}
			&:not(:last-child){
				margin-bottom: 0;
			}
			&:nth-child(odd){
				border-right: 1px solid $colorBg;
				margin-right: -1px;
				&:after{
					left: 100%;
					transform: translateX(-50%);
				}
				&:last-child{
					border-right: 0;
					&:after{
						left: 50%;
					}
					.footer-box{
						&__head{
							padding-right: 0;
						}
						&__body{
							padding-right: 0;
						}
					}
				}
				.footer-box{
					&__head{
						padding-right: 30px;
					}
					&__body{
						padding-right: 30px;
					}
				}
			}
			&:nth-child(even){
				&:after{
					left: 0;
					transform: translateX(-50%);
				}
				.footer-box{
					&__head{
						padding-left: 30px;
					}
						&__arr{
							margin-right: -25px;
						}
					&__body{
						padding-left: 30px;
					}
				}
			}
			&.show{
				.footer-box{
					&__head{
						.icon{
							transform: rotateX(180deg);
						}
					}
				}
			}
			&__head{
				line-height: 1.4;
				padding: 20px 0;
				margin-bottom: 0;
			}
				&__arr{
					display: flex;
				}
			&__body{
				ul{
					display: none;
					margin-bottom: 30px;
				}
			}
		}
		&-menu{
			padding: 0;
			&:before{
				content: none;
			}
			.footer-box{
				&__head{
					display: flex;
				}
				&__body{
					ul{
						display: none;
						li{
							&:not(:last-child) {
								margin-bottom: 20px;
							}
							a{
								color: $colorGrey;
								font-size: 13px;
								font-weight: 400;
								&:hover{
									color: $colorText;
								}
							}
						}
					}
				}
			}
		}
	}
	.subscribe{
		&-form{
			flex-direction: column;
			.form{
				&-group{
					margin-bottom: 20px;
				}
				&-button{
					.btn{
						width: 100%;
					}
				}
			}
		}
		form{
			&:not(:last-child){
				margin-bottom: 20px;
			}
		}
	}
	.socials{
		a{
			width: 50px;
			height: 50px;
		}
	}
	.payment{
		img{
			max-width: 55px;
			max-height: 30px;
		}
	}
	.content{
		padding: 40px 0;
	}
	.blog{
		&-list{
			padding-top: 40px;
			&__box{
				margin-bottom: 40px;
			}
		}
		&-details{
			margin-bottom: 40px;
			&__text{
				&:not(:last-child){
					margin-bottom: 40px;
				}
				img,
				table,
				blockquote{
					margin: 40px auto;
				}
				blockquote{
					padding: 40px;
					p{
						&:not(:last-child){
							margin-bottom: 40px;
						}
					}
				}
				.table-responsive{
					margin: 40px 0;
				}
			}
		}
	}
	.catalog{
		&-category{
			margin-bottom: 30px;
			ul{
				li{
					a{
						width: 160px;
						height: 60px;
						font-size: 15px;
					}
				}
			}
		}
		&-section{
			margin-top: 80px;
		}
		&-sort{
			margin-bottom: 40px;
		}
		&-list{
			&:not(:first-child){
				padding-top: 40px;
			}
		}
		&-collection{
			padding-top: 40px;
			&__box{
				margin-bottom: 40px;
				&-title{
					font-size: 24px;
				}
				&-link{
					bottom: 40px;
					.btn{
						width: 220px;
					}
				}
			}
			&__details{
				display: block;
				&-poster{
					width: 480px;
					margin-right: 0;
					margin-bottom: 40px;
				}
				&-list{
					&:not(:first-child){
						width: 680px;
					}
				}
					&-box{
						margin-bottom: 40px;
					}
			}
		}
	}
	.product{
		margin-bottom: 40px;
		&-card{
			margin-bottom: 40px;
			&__gallery{
				display: block;
				&-big{
					width: auto;
					margin-bottom: 15px;
				}
				&-small{
					width: auto;
					margin: 0 -5px;
					.product-card__gallery-img{
						margin: 0 5px;
					}
					.slick{
						&-track{
							margin-left: 0;
						}
					}
				}
			}
			&__content{
				padding: 10px 0 0;
			}
			&__desc{
				&-details{
					p{
						&:not(:last-child){
							margin-bottom: 30px;
						}
					}
				}
			}
			&__sizes{
				width: 100%;
				a{
					padding: 5px 0;
					&:not(:last-child) {
						margin-right: 30px;
