				li{
					margin: 0 5px 10px;
					a{

					}
				}
			}
		}
		&-sort{
			&__box{
				margin-bottom: 20px;
				&:not(:first-child){
					margin-left: 0;
				}
				&-title{
					flex-grow: 1;
					flex-shrink: 0;
					flex-basis: 0;
				}
			}
		}
		&-filter{
			&__inner{
				width: 100vw;
				right: -100vw;
				padding: 20px 15px;
			}
			&__tag{
				font-size: 12px;
			}
			&__toggle{
				width: 100%;
				.btn{
					width: 100%;
					font-size: 14px;
					.icon{
						margin-right: 10px;
					}
				}
			}
			&__close{
				top: 20px;
				right: 15px;
			}
		}
		&-list{
			padding-left: 8px;
			padding-right: 8px;
			.row{
				[class*="col-"]{
					padding: 0 8px;
				}
			}
		}
		&-collection{
			&__details{
				&-slider{
					padding: 0 8px;
					.product{
						margin: 0 8px;
					}
				}
			}
		}
	}
	.product{
		&-card{
			&__button{
				&-row{
					flex-direction: column;
				}
				&-col{
					width: auto;
					&:not(:last-child){
						margin-right: 0;
						margin-bottom: 15px;
					}
				}
			}
			&__modal{
				.product-card{
					&__button{
						&-row{
							flex-direction: column;
						}
						&-col{
							&:not(:last-child){
								margin-right: 0;
								margin-bottom: 15px;
							}
						}
					}
				}
			}
			&__quick{
				.product-card{
					&__body{
						.form{
							&-button{
								.btn{
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
		&-labels{
			top: 15px;
			width: 50px;
		}
			&-label{
				font-size: 10px;
				padding: 3px 5px;
			}
		&-action{
			padding: 15px 5px;
			.btn{
				width: 40px !important;
				height: 40px !important;
				line-height: 40px !important;
			}
		}
		&-body{
			padding: 15px 0 0;
		}
			&-title{
				a{
					font-size: 13px;
				}
			}
			&-rating{
				margin-bottom: 5px;
			}
			&-price{
				font-size: 13px;
			}
		&-link{
			a{
				font-size: 13px;
			}
		}
		&-options{
			&__color{
				.check{
					[type="radio"],
					[type="checkbox"]{
						& ~ label {
							padding: 0 10px;
						}
					}
				}
			}
			&__size{
				.select2{
					&-container{
						&--default{
							.select2-selection{
								&--single{
									&.select-xs{
										.select2-selection{
											&__arrow{
												width: 25px;
											}
										}
									}
									.select2-selection{
										&__rendered{
											padding-left: 10px;
											padding-right: 25px;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
	.rating{
		.icon{
			font-size: 11px;
			&:not(:last-child){
				margin-right: 1px;
			}
		}
	}
	.checkout{
		&-registration{
			.form{
				&-button{
					margin-bottom: 20px;
				}
			}
		}
		&-list{
			max-height: none;
			&__product{
				display: block;
				&-head{
					width: auto;
					margin-right: 0;
					margin-bottom: 20px;
				}
					&-img{
						img{
							width: 100%;
						}
					}
				&-price,
				&-title,
				&-count,
				&-calc{
					margin-bottom: 10px;
				}
				&-options{
					margin-bottom: 5px;
				}
			}
		}
		&-button{
			.col-sm-6{
				&:not(:last-child){
					margin-bottom: 20px;
				}
			}
		}
	}
	.size{
		&-category{
			&__tabs{
				margin-bottom: 20px;
				ul{
					flex-wrap: wrap;
					li{
						width: 50%;
						margin-bottom: 10px;
						&:not(:last-child){
							margin-right: 0;
						}
					}
				}
				.checkbox{
					[type="checkbox"],
					[type="radio"]{
						& ~ label{
							&:not(:empty){
								padding-left: 30px;
							}
						}
					}
				}
			}
			&__details{
				&-list{
					ul{
						column-count: 2;
					}
				}
			}
		}
	}
	.similars{
		&-list{
			&__box{
				&-tabs{
					ul{
						li{
							flex-grow: 1;
							flex-shrink: 0;
							flex-basis: 0;
							text-align: center;
							padding: 0 8px 5px;
							&:not(:last-child){
								margin-right: 0;
							}
						}
					}
				}
				&-slider{
					padding: 0 8px;
					.slick{
						&-arrow{
							top: calc(((100vw - 30px) / 2 - 16px) / 2);
						}
						&-prev{
							left: 0;
						}
						&-next{
							right: 0;
						}
					}
					.product{
						margin: 0 8px;
					}
				}
			}
		}
	}
	.wishlist{
		&-list{
			padding-left: 8px;
			padding-right: 8px;
			.row{
				[class*="col-"]{
					padding: 0 8px;
				}
			}
		}
	}
	.search{
		&-section{
			&__list{
				padding-left: 8px;
				padding-right: 8px;
				.row{
					[class*="col-"]{
						padding: 0 8px;
					}
				}
			}
		}
		&-form{
			&__list{
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				.product{
					width: calc(50% - 8px);
				}
			}
		}
	}
}
