::-webkit-input-placeholder {
	color: $colorLigthText;
	opacity: 1;
}
::-moz-placeholder {
	color: $colorLigthText;
	opacity: 1;
}
:-ms-input-placeholder {
	color: $colorLigthText;
	opacity: 1;
}
:-moz-placeholder {
	color: $colorLigthText;
	opacity: 1;
}
input,
textarea {
	position: relative;
	background: $colorWhite;
	color: $colorDarkGrey;
	width: 100%;
	font-size: 14px;
	font-family: 'Evolventa', sans-serif;
  	border: 1px solid $colorBg;
	border-radius: 0;
	transition: all 0.3s ease;
	padding: 0 20px;
	appearance: none;
	mix-blend-mode: normal;
	opacity: 0.8;
	&:hover {
		border-color: $colorLigthText;
	}
	&:active,
	&:focus{
    &:not(.btn){
      &:not([disabled]){
        &::-webkit-input-placeholder {
          color: transparent;
        }
        &::-moz-placeholder {
          color: transparent;
        }
        &:-ms-input-placeholder {
          color: transparent;
        }
        &:-moz-placeholder {
          color: transparent;
        }
      }
    }
	}
	&[disabled] {
		opacity: 0.5;
	}
}
input{
	height: 56px;
	text-overflow: ellipsis;
	overflow: hidden;
}
textarea{
	vertical-align: top;
	height: 170px;
	padding: 15px 20px;
	resize: none;
}
.has{
	&-error{
		input,
		textarea{
			border-bottom-color: $colorRed !important;
			//&::-webkit-input-placeholder {
			//	color: $colorRed !important;
			//}
			//&::-moz-placeholder {
			//	color: $colorRed !important;
			//}
			//&:-ms-input-placeholder {
			//	color: $colorRed !important;
			//}
			//&:-moz-placeholder {
			//	color: $colorRed !important;
			//}
		}
		.help-block{
			display: block;
		}
	}

	&-success {
		&:before{
			content: '\EA0F';
			position: absolute;
			top: 50%;
			right: 10px;
			color: $colorGreen;
			font-size: 12px;
			font-family: 'svgfont', sans-serif;
			transform: translateY(-50%);
			pointer-events: none;
			z-index: 1;
		}
		//input,
		//textarea {
		//	color: $colorGreen !important;
		//	border-color: $colorGreen !important;
		//}
	}
}
.form{
	&-box{
		position: relative;
		background: $colorWhite;
		color: $colorDarkGrey;
		&:not(:last-child){
			margin-bottom: 30px;
		}
	}
		&-head{
			position: relative;
			display: flex;
			padding: 30px;
		}
		&-body{
			position: relative;
			padding: 30px;
			&:not(:first-child){
				border-top: 1px solid #D4D4D4;
			}
		}
	&-title{
		position: relative;
		flex-grow: 1;
		flex-shrink: 0;
		flex-basis: 0;
		color: $colorDarkGrey;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 0.0625em;
		text-transform: uppercase;
		&:not(:last-child){
			margin-right: 30px;
		}
	}
	&-body{
		position: relative;
	}
	&-text{
		position: relative;
	}
	&-group{
		position: relative;
		&:not(:last-child){
			margin-bottom: 20px;
		}
		& > label{
			position: relative;
			display: block;
			color: $colorDarkGrey;
			font-size: 14px;
			font-weight: 700;
			letter-spacing: 0.0725em;
			text-transform: uppercase;
			margin-bottom: 20px;
		}
		& > .checkbox{
			display: block;
			margin-bottom: 20px;
		}
		&.d-flex{
			&:not(:first-child){
				margin-top: 50px;
			}
			&:not(:last-child){
				margin-bottom: 50px;
			}
			& > label{
				margin-bottom: 0;
				margin-right: 50px;
			}
		}
	}
	&-flex{
		position: relative;
		display: flex;
		align-items: flex-start;
		&:not(:last-child){
			margin-bottom: 20px;
		}
		.form{
			&-group{
				flex-grow: 1;
				flex-shrink: 0;
				flex-basis: 0;
				margin-bottom: 0;
			}
		}
	}
	&-button{
		position: relative;
		&:not(:last-child){
			margin-bottom: 20px;
		}
	}
	&-icon{
		position: relative;
		input{
			padding-left: 50px;
		}
		[class^="icon"]{
			position: absolute;
			top: 14px;
			left: 20px;
			color: #c8c8c8;
			font-size: 20px;
			pointer-events: none;
		}
	}
}
.checkbox{
	position: relative;
	display: inline-block;
	&-list{
		position: relative;
		display: flex;
		flex-wrap: wrap;
		&.flex-column{
			.checkbox{
				&:not(:last-child){
					margin-bottom: 20px;
				}
			}
		}
		&:not(.flex-column){
			.checkbox{
				&:not(:last-child){
					margin-right: 20px;
				}
			}
		}
	}
	[type="checkbox"],
	[type="radio"]{
		position: absolute;
		left: -9999px;
		width: inherit;
		height: inherit;
		& ~ label{
			position: relative;
			display: inline-block;
			min-height: 18px;
			color: $colorDarkGrey;
			font-size: 12px;
			line-height: 18px;
			padding-left: 18px;
			margin: -5px 0;
			cursor: pointer;
			transition: all 0.3s ease;
			opacity: 0.7;
			&:before{
				content: '';
				position: absolute;
				background: transparent;
				top: 0;
				left: 0;
				width: 18px;
				height: 18px;
				border: 1px solid $colorBg;
				border-radius: 0;
				transition: all 0.3s ease;
			}
			&:after{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				transition: all 0.3s ease;
				opacity: 0;
			}
			&:hover{
				opacity: 1;
				&:before{
					border-color: $colorLigthText;
				}
			}
			&:not(:empty){
				margin: 0;
				padding-left: 40px;
			}
		}
		&:disabled{
			& ~ label{
				&:before{

				}
				&:after{
					opacity: 0;
				}
			}
		}
		&:checked{
			& ~ label{
				&:before{

				}
				&:after{
					opacity: 1;
				}
			}
			&:disabled{
				& ~ label{
					&:before{

					}
					&:after{
						opacity: 0.2;
					}
				}
			}
		}
	}
	[type="checkbox"]{
		& ~ label{
			&:after{
				content: '\EA0F';
				width: 18px;
				height: 18px;
				color: $colorBlack;
				font-size: 8px;
				font-family: 'svgfont', sans-serif;
				line-height: 18px;
				text-align: center;
			}
		}
	}
	[type="radio"]{
		& ~ label{
			min-height: 20px;
			line-height: 20px;
			padding-left: 40px;
			&:before,
			&:after{
				border-radius: 50%;
			}
			&:before{
				width: 20px;
				height: 20px;
			}
			&:after{
				background: $colorBlack;
				top: 4px;
				left: 4px;
				width: 12px;
				height: 12px;
			}
		}
		&:checked{
			& ~ label{
				&:before{

				}
		  }
		}
	}
	&.has{
		&-error{
			[type="checkbox"],
			[type="radio"]{
				& ~ label{
					color: $colorRed !important;
					&:before{
						border-color: $colorLightRed !important;
					}
				}
			}
		}
	}
}
.check-lg{
	[type="checkbox"],
	[type="radio"]{
		& ~ label{
			min-height: 26px;
			font-size: 14px;
			line-height: 26px;
			padding-left: 26px;
			&:before{
				width: 26px;
				height: 26px;
			}
		}
	}
	[type="checkbox"]{
		& ~ label {
			&:after {
				width: 26px;
				height: 26px;
				line-height: 26px;
			}
		}
	}
}
.btn-switch{
	position: relative;
	display: inline-block;
	vertical-align: middle;
	[type="checkbox"]{
		position: absolute;
		left: -9999px;
		width: inherit;
		height: inherit;
		& ~ label{
			position: relative;
			display: inline-block;
			background: $colorBg;
			height: 6px;
			width: 24px;
			border-radius: 3px;
			margin: 3px 0;
			transition: all 0.3s ease;
			cursor: pointer;
			&:before{
				content: '';
				position: absolute;
				background: $colorBlack;
				top: -3px;
				left: 0;
				width: 12px;
				height: 12px;
				border-radius: 50%;
				transition: all 0.3s ease;
			}
			&:after{
				content: '';
				position: absolute;
				background: $colorBlack;
				height: 6px;
				width: 0;
				border-radius: 3px;
				transition: all 0.3s ease;
			}
		}
		&:checked{
			& ~ label{
				&:before{
					left: calc(100% - 12px);
				}
				&:after{
					width: 24px;
				}
			}
		}
	}
}
select{
	background: transparent;
	border: 1px solid $colorBg;
}
.help-block{
	position: absolute;
	display: none;
	top: 100%;
	width: 100%;
	color: $colorRed;
	font-size: 8px;
	line-height: 1.5em;
	letter-spacing: 0.0625em;
	text-transform: uppercase;
	padding: 5px 20px;
}


.file {
	&-box {
		position: relative;
		&__field {
			&-title {

			}
		}
		&__list {
			position: relative;
			&-box {
				position: relative;
				display: flex;
				align-items: center;
				margin-top: 10px;
				&-name {
					position: relative;
					max-width: calc(100% - 25px);
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
				}
				&-delete {
					position: relative;
					color: $colorBlack;
					font-size: 12px;
					transition: color 0.3s ease;
					cursor: pointer;
					&:last-child{
						margin-left: 10px;
					}
					&:hover{
						color: $colorDarkGrey;
					}
					.icon{
						top: -2px;
					}
				}
			}
		}
		&__field {
			position: relative;
			&.hidden{
				display: none;
			}
			&-title {
				position: relative;
				cursor: pointer;
				[type='file'] {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					opacity: 0;
				}
			}
		}
	}
}
